html, body {
  margin:0;
  padding:0;
  min-height:100%;
  width:100%;
  box-sizing:border-box;
  background-color:#f0f4f9 !important;
}

// Remove padding on tablet devices
@media only screen and (min-device-width: 1112px) {

  #container { 
    padding:20px;
  }
   
}

#app {
  min-height:100%;
  max-width:1400px;
  margin:0 auto;
  border-radius:3px;
  overflow:hidden;
  background-color:#fff;
  box-shadow:0 0 4px rgba(0,0,0,0.05);
}

h1 {
  color:#d23500;
}